.contact {
  height: inherit;
  display: flex;
}

.contact .leftSide {
  flex: 50%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.contact .rightSide {
  flex: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contact .rightSide h1 {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

form {
  display: flex;
  flex-direction: column;
  padding: 30px;
}

form input {
  height: 40px;
  width: 80%;
  border: none;
  border-bottom: 1px solid #121619;
}

form textarea {
  margin-top: 15px;
  width: 80%;
  border: none;
  border-bottom: 1px solid #121619;
}
input::placeholder,
textarea::placeholder {
  font-weight: bold;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}
form input:focus,
textarea:focus {
  outline: none;
}

form label {
  margin-top: 15px;
  color: grey;
}

form button {
  margin-top: 40px;
  width: 140px;
  height: 40px;
  border: none;
  background-color: darkred;
  color: black;
  font-size: 15px;
  cursor: pointer;
}

form button:hover {
  background-color: indianred;
  cursor: pointer;
  transition: 0.3s ease-in;
}